export function ExecuteREGLCommand(regl, drawCommand, vertexAttrType) {
  drawCommand({
    position: vertexAttrType.position,
    time: vertexAttrType.time,
    mousePos: vertexAttrType.mousePos,
    isMouseEnable: vertexAttrType.isMouseEnable,
    textureLerpValue: vertexAttrType.textureLerpValue
  });
}
export function CreateREGLCommandObj(regl, vertex, fragment, noiseTex, frontTexA, highlightTexA, frontTexB, highlightTexB, a_uv, scale, minRevealRange, vertex_count) {
  return regl({
    frag: fragment,
    vert: vertex,
    attributes: {
      a_position: regl.prop("position"),
      a_uv
    },
    uniforms: {
      u_scale: scale,
      u_noise_tex: regl.texture({data: noiseTex, wrap: "repeat"}),
      u_front_tex_a: frontTexA,
      u_highlight_tex_a: highlightTexA,
      u_front_tex_b: frontTexB,
      u_highlight_tex_b: highlightTexB,
      u_min_reveal_range: minRevealRange,
      u_time: regl.prop("time"),
      u_mousePos: regl.prop("mousePos"),
      u_isMouseEnable: regl.prop("isMouseEnable"),
      u_textureLerpValue: regl.prop("textureLerpValue")
    },
    count: vertex_count
  });
}
